<script>
	export default{
		name: 'footerPart',
		mounted() {
			//footer
		    $(".cityName li").click(function() {
		      $(this).addClass("active").siblings().removeClass("active");
		      var _name = $(this).attr("data-action");
		      $("#footer .tabcontent ." + _name + "").addClass("active").siblings().removeClass("active");
		    })

		    $(".map_point").click(function() {
		      $(".mapDv").show();
		      var _point = $(this).attr("data-map").split(",");
		      $("#baiduMap").show();
		        // API
		        var map = new BMap.Map("baiduMap", {});
		        map.enableScrollWheelZoom(true);
		        var point = new BMap.Point(_point[0], _point[1]);
		        map.centerAndZoom(point, 50);
		        // API
		        var sContent =
		        "<h4 style='margin:0 0 5px 0;padding:0.2em 0'></h4>" +
		        "<img style='display:block;margin:4px 0' id='imgDemo' src='static/images/footer/shanghai.jpg' width='100%' />" +
		        "<p style='margin:0;line-height:1.5;font-size:13px;text-indent:2em'>11100%...</p>" +
		        "</div>";

		        var infoWindow = new BMap.InfoWindow(sContent); // 


		        //logo
		        var pt = new BMap.Point(_point[0], _point[1]);
		        var myIcon = new BMap.Icon("../../../static/images/footer/mappoint.png", new BMap.Size(95, 36));
		        var marker2 = new BMap.Marker(pt, { icon: myIcon }); // 
		        map.addOverlay(marker2);

		      })

		    $(".map_close").click(function(){
		      $(".mapDv").hide();
		    })
		}
	}
</script>
